import React, { useEffect } from 'react';
import { Box, ModalUnstyled, Rating, Stack } from '@mui/material';
import { useParams } from 'react-router-dom';
import { connect, useDispatch, useSelector } from 'react-redux';
import { State } from '../redux/reducers/root-reducer';
import { Genre, TvShowDetails } from '../types';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import HistoryIcon from '@mui/icons-material/History';
import StarIcon from '@mui/icons-material/Star';
import getTvShow from '../redux/actions/tvShow.actions';
import BackButton from '../components/BackButton';
import '../styles/MoviePage.scss';
import PopOver from '../components/PopOver';
import Spinner from '../components/Spinner';

const TvShowPage = () => {
  const dispatch = useDispatch();
  const { id } = useParams();
  const tvShowId = String(id);

  useEffect(() => {
    dispatch(getTvShow(tvShowId));
  }, [dispatch, tvShowId]);

  const { tvShow, fetched }: { tvShow: TvShowDetails; fetched: boolean } =
    useSelector((state: State) => state.tvShow);

  const {
    backdrop_path,
    poster_path,
    tagline,
    name,
    overview,
    number_of_seasons,
    seasons,
    genres,
    first_air_date,
    episode_run_time,
    vote_average,
  } = tvShow;

  const base_url_for_backdrop = 'https://image.tmdb.org/t/p/w500';
  const base_url_for_poster = 'https://image.tmdb.org/t/p/w185';

  return (
    <ModalUnstyled open={true} className='movie-page'>
      <div style={{ maxHeight: 'fit-content' }}>
        {fetched ? (
          <Box
            className='container'
            sx={{
              backgroundImage: `url(${
                backdrop_path !== null
                  ? base_url_for_backdrop + backdrop_path
                  : base_url_for_poster + poster_path
              })`,
              backgroundRepeat: 'no-repeat',
              backgroundSize: 'cover',
              backgroundPositionY: '40%',
            }}
          >
            <Box
              className='inside-box'
              sx={{
                position: 'fixed',
                top: 0,
                width: '100%',
                height: '100%',
                backgroundColor: 'rgba(0, 0, 0,  0.3)',
              }}
            >
              <div className='group'>
                <div
                  className='title_and_rating'
                  style={{
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems: 'center',
                  }}
                >
                  <h2>{name} </h2>
                  <Stack spacing={1}>
                    <Rating
                      value={vote_average * 0.5}
                      precision={0.1}
                      max={5}
                      emptyIcon={
                        <StarIcon
                          style={{ opacity: 1, color: '#fff' }}
                          fontSize='inherit'
                        />
                      }
                      readOnly
                    />
                  </Stack>
                </div>
                <div className='img-and-inf'>
                  <div className='image'>
                    <img
                      src={
                        poster_path !== null
                          ? base_url_for_poster + poster_path
                          : `data:image/png;base64,`
                      }
                      alt={name}
                    />
                  </div>

                  <div className='info'>
                    <h3 className='tagline'>{tagline}</h3>
                    <div className='about'>{overview}</div>
                    {number_of_seasons > 0 && (
                      <div className='no-of-seasons'>
                        Seasons: {number_of_seasons}
                      </div>
                    )}
                    {seasons && (
                      <div className='seasons'>
                        {seasons.map((season, index) => {
                          return <PopOver key={index} season={season} />;
                        })}
                      </div>
                    )}
                  </div>
                </div>
                <div className='details'>
                  <div className='genres'>
                    {genres.length > 0 && (
                      <strong className='strong'>Genre: </strong>
                    )}
                    {genres.map((gen: Genre) => {
                      return (
                        <span className='genre' key={gen.id}>
                          {gen.name}
                        </span>
                      );
                    })}
                  </div>

                  {first_air_date && (
                    <div className='duration'>
                      <AccessTimeIcon />
                      <strong className='strong'>Released: </strong>
                      <div className='release_and_duration'>
                        {` ${first_air_date}`}
                      </div>
                    </div>
                  )}
                  {episode_run_time[0] && (
                    <div className='duration'>
                      <HistoryIcon />
                      <strong className='strong'>Duration: </strong>
                      <div className='release_and_duration'>
                        {episode_run_time[0]} minutes
                      </div>
                    </div>
                  )}
                </div>
              </div>
              <BackButton />
            </Box>
          </Box>
        ) : (
          <Spinner loading={fetched} />
        )}
      </div>
    </ModalUnstyled>
  );
};

const mapStateToProps = (state: State) => ({
  tvShow: state.tvShow.tvShow,
  fetched: state.tvShow.fetched,
});

export default connect(mapStateToProps)(TvShowPage);
